.queuenumbercontainer {
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  padding: 0;
  margin: 0;
  width: 90%;
  height: 140px;
  border-radius: 19px;
  margin: auto;
  overflow: visible;
  background-image: url(wave.png), url(rectangle.png);
  background-repeat: no-repeat;
  background-size: 100vw 70px, 100vw 230px;
  background-position: bottom, top;
}
.texts {
  flex-direction: column;
  margin: 0;
}
.textcenter {
  text-align: center;
  font-size: 1.2rem;
  font-weight: 600;
}
.flexcolumn {
  flex-direction: column;
}
.queuenumberman {
  padding: 0;
  margin: 0;
  position: absolute;
  right: 0;
}
.row {
  position: relative;
  margin: 0;
  padding: 0;
  width: 100%;
}
.fontstyle1 {
  font-family: Roboto;
  font-style: normal;
  font-weight: 700;
  font-size: 21px;
  line-height: 25px;
  letter-spacing: -0.3px;
  color: #595959;
}
.fontstyle1small {
  font-family: Roboto;
  font-style: normal;
  font-size: 18px;
  line-height: 24px;
  letter-spacing: -0.3px;
  font-weight: 700;
  color: #595959;
}
.rectanglebackground {
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  background: #f2fbff;
  margin-bottom: 10px;
  background-repeat: no-repeat;
  background-size: 100vw 75px;
  background-position: top;
  border-radius: 5px;
}
.rectanglebackground2 {
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  background: #f2fbff;
  margin-bottom: 10px;
  background-repeat: no-repeat;
  background-size: 100vw 95px;
  background-position: top;
  border-radius: 5px;
  margin-right: 10px;
}
.rectanglebackground2_1 {
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  background: #f2fbff;
  margin-bottom: 10px;
  background-repeat: no-repeat;
  background-size: 100vw 95px;
  background-position: top;
  border-radius: 5px;
  margin-left: 10px;
}
.shine {
  pointer-events: none;
  overflow-x: hidden;
  overflow-y: visible;
}
.shine:after {
  content: "";
  pointer-events: none;
  top: 0;
  transform: translateX(100%);
  width: 100%;
  height: 100%;
  position: absolute;
  background: linear-gradient(to right, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.8) 50%, rgba(128, 186, 232, 0) 99%, rgba(125, 185, 232, 0) 100%);
  z-index: 1;
  animation: slide 1s infinite;
}
@keyframes slide {
  0% {
    transform: translateX(-100%);
 }
  100% {
    transform: translateX(100%);
 }
}
